import React from 'react';
import Home from '../home/home';
import Dashboard from '../dashboard/dashboard';
import About from '../about/about';
import Chartt from '../chartjs/chartt';
import styles from './layout.module.scss';

import {
    HashRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";


function Layout() {




  return (
    <Router>
    <div className={styles.containerheader}>
      <div className={styles.child}>
        <Link className={styles.nodeco} to="/">Home</Link>
      </div>
      <div className={styles.child}>
        <Link className={styles.nodeco} to="/about">About</Link>
      </div>
      <div className={styles.child}>
        <Link className={styles.nodeco} to="/dashboard">Dashboard</Link>
      </div>
      <div className={styles.child}>
        <Link className={styles.nodeco} to="/chart">Charts</Link>
      </div>
    </div>
    <Switch>
      <Route exact path="/">
        <Home />
      </Route>
      <Route path="/about">
        <About />
      </Route>
      <Route path="/dashboard">
        <Dashboard />
      </Route>
      <Route path="/chart">
        <Chartt />
      </Route>


    </Switch>
  </Router>
  );
}


export default Layout;